import {
  Button,
  createMuiTheme,
  TextField,
  ThemeProvider,
} from "@material-ui/core";
import "./Search.css";
import SearchIcon from "@material-ui/icons/Search";
import React, { useEffect, useState } from "react";
import axios from "axios";
import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";
import Fuse from "fuse.js";
import sha1 from 'crypto-js/sha1';
import utf8 from 'utf8';

const Search = () => {
  const [searchText, setSearchText] = useState("");
  const [page, setPage] = useState(1);
  const [content, setContent] = useState();
  const [numOfPages, setNumOfPages] = useState(1);
  const [cdn, setCdn] = useState('');
  const [db, setDb] = useState();

  const darkTheme = createMuiTheme({
    palette: {
      type: "dark",
      primary: {
        main: "#fff",
      },
    },
  });

  const fetchSearch = async () => {
    if (searchText === '') {
      return;
    }
    const searchResult = db.search(searchText).map((result)=>result.item);
    var searchContent = [];
    for(const v of searchResult)  {
      const resHash = sha1(utf8.encode(v)).toString();
      console.log(resHash);
      try {
        const {data} = await axios.get(
          // `${cdn}output/${resHash}`
          `${process.env.REACT_APP_TEST_BASEDIR}output/${resHash}`
        );
        searchContent.push(data);
      }catch(err) {
        console.log(err);
      }
    }
    setContent(searchContent);
  };

  const initSearchJson = async () => {
    if (db) {
      return;
    }
    if (cdn !== '') {
      try {
        const {data} = await axios.get(
          // `${cdn}search.json`
          `${process.env.REACT_APP_TEST_BASEDIR}search.json`
        );
        setDb(new Fuse(utf8.encode(data)));
      } catch(err) {
        setDb(new Fuse([]));
        console.log(err);
      }
      
    } else {
      await axios.get(
        process.env.REACT_APP_CONFIG_URL
      ).then( async function(res) {
        setCdn(res.data.cdn);
        try {
          const { data } = await axios.get(
            // `${res.data.cdn}search.json`
            `${process.env.REACT_APP_TEST_BASEDIR}search.json`
          );
          setDb(new Fuse(utf8.encode(data)));
        }catch(err) {
          setDb(new Fuse([]));
          console.log(err);
        }
      });
    }
  };

  useEffect(() => {
    window.scroll(0, 0);
    initSearchJson();
    fetchSearch();
    // eslint-disable-next-line
  }, [page]);

  return (
    <div>
      <ThemeProvider theme={darkTheme}>
        <div className="search">
          <TextField
            style={{ flex: 1 }}
            className="searchBox"
            label="Search"
            variant="filled"
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button
            onClick={fetchSearch}
            variant="contained"
            style={{ marginLeft: 10 }}
          >
            <SearchIcon fontSize="large" />
          </Button>
        </div>
      </ThemeProvider>
      <div className="trending">
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.slug}
              id={c.slug}
              poster={c.cover}
              title={c.title}
              date={new Date(c.update_at).toLocaleDateString()}
              media_type="video"
              vote_average='9.0'
              duration={c.duration}
              url={c.url}
              backurl="/search"
            />
          ))}
      </div>
      {numOfPages > 1 && (
        <CustomPagination setPage={setPage} numOfPages={numOfPages} />
      )}
    </div>
  );
};

export default Search;
