import axios from "axios";
import { useEffect, useState } from "react";
import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";
import "./Series.css";

const Series = () => {
  const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);
  const [numOfPages, setNumOfPages] = useState();
  const [cdn, setCdn] = useState('');
  const [googleId, setGoogldId] = useState('');

  const fetchSeries = async () => {
    const pageFormatNum = String(page-1).padStart(3, '0');
    if (cdn !== '') {
      try {
        const {data} = await axios.get(
          // `${cdn}page-${pageFormatNum}.json`
          `${process.env.REACT_APP_TEST_BASEDIR}page-${pageFormatNum}.json`
        );
        setContent(data);
        if (page+1 > numOfPages) {
          setNumOfPages(page+1);
        }
      } catch(err) {
        setContent([]);
        console.log(err);
      }
      
    } else {
      await axios.get(
        process.env.REACT_APP_CONFIG_URL
      ).then( async function(res) {
        setCdn(res.data.cdn);
        setGoogldId(res.data.google_statistics_id);
        try {
          const { data } = await axios.get(
            // `${res.data.cdn}page-${pageFormatNum}.json`
            `${process.env.REACT_APP_TEST_BASEDIR}page-${pageFormatNum}.json`
          );
          setContent(data);
          if (page+1 > numOfPages) {
            setNumOfPages(page+1);
          }
        }catch(err) {
          setContent([]);
          console.log(err);
        }
      });
    }
  };

  useEffect(() => {
    window.scroll(0, 0);
    fetchSeries();
    // eslint-disable-next-line
  }, [page]);

  return (
    <div className="mContent">
      <div className="trending">
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.slug}
              id={c.slug}
              poster={c.cover}
              title={c.title}
              date={c.update_at}
              media_type="video"
              vote_average='9.0'
              duration={c.duration}
              url={c.url}
              backurl="/"
            />
          ))}
      </div>
      {numOfPages > 1 && (
        <CustomPagination setPage={setPage} numOfPages={numOfPages} />
      )}
    </div>
  );
};

export default Series;
